.kanban{
  display: flex;
  align-items: flex-start;
  &__section{
    width: 400px;
    background-color: #3c2657;
    padding: 10px;
    border-radius: 10px;
    &~&{
      margin-left: 10px;
    }
    &__title{
      font-size: 1.2rem;
      margin: 10px 0 20px;
    }
    &__content{
      &>*~*{
        margin-top: 10px;
      }
    }
  }
}